// Common SCSS

body {
    background-color: $white;
}

main {
    position: relative;
    overflow: hidden;
    z-index: 10;
}

.hide-scroll {
    overflow: hidden;
    height: 100vh;
}

.font-normal {
    font-weight: 400;
}

.font-medium {
    font-weight: 500;
}

.font-semibold {
    font-weight: 600;
}

.font-bold {
    font-weight: 700;
}

.font-extrabold {
    font-weight: 800;
}

.text-primary {
    color: $primary;
}

.text-secondary {
    color: $secondary;
}

.text-neutral-500 {
    color: $neutral-500;
}

.bg-primary {
    background-color: $primary;
}

.bg-secondary {
    background-color: $secondary;
}
.bg-orange {
    background-color: $orange;
}
.bg-black {
    background-color: $neutral-900;
}

.reveal-image {
    position: relative;
    --scaleX: 1;
    --scaleFull: 1.2;
    overflow: hidden;
    &:before {
        background-color: $white;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        content: '';
        transform: scaleX(var(--scaleX));
        transform-origin: left;
        transition: 0.5s all ease-out;
        z-index: 10;
    }
    & img {
        transition: 0.5s all ease-out;
        transform: scale(var(--scaleFull));
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.text-underline {
    position: relative;
    display: inline-flex;
    & svg {
        // background-image: url(../assets/images/svg/underline.svg);
        // width: 164px;
        // height: 17px;
        // content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: -10px;
        // background-size: contain;
        // background-position: center center;
        // background-repeat: no-repeat;
        @include tablet {
            width: 114px;
            height: 12px;
        }
    }
}

.moving-item {
    will-change: transform;
}