@keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes moveLeft {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes scroll-first {
  0% {
    transform: translateX(var(--anim-width));
  }
  to {
    transform: translateX(calc(-1 * var(--anim-width)));
  }
}
@keyframes scroll-second {
  0% {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc(-1 * var(--anim-width) * 2));
  }
}
