}
.collapse:not(.active) {
  height: 0;
}

.tab-content {
  display: none;
  animation: fadeIn 600ms cubic-bezier(0.49, 0.025, 0.49, 1);
}
.tab-content.active {
  display: block;
}

[data-collapse] .collapse-arrow,
[data-toggler] .collapse-arrow {
  transition: 500ms all cubic-bezier(0.76, 0, 0.24, 1);
  transform: rotate(-90deg);
}
[data-collapse].active .collapse-arrow,
[data-toggler].active .collapse-arrow {
  transform: rotate(0deg);
}

.link-effect {
  position: relative;
  white-space: nowrap;
}
.link-effect::before, .link-effect::after {
  bottom: 2px;
  width: 100%;
  transform: scaleX(0);
  height: 1px;
  will-change: transform;
  left: 0;
  background-color: currentColor;
  transition: transform 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.link-effect::before {
  opacity: 0;
  transform-origin: left;
}
.link-effect::after {
  opacity: 1;
  transform-origin: right;
}
.link-effect:hover::before, .link-effect:hover::after {
  transform: scaleX(1);
}
.link-effect:hover::before {
  opacity: 1;
}
.link-effect:hover::after {
  opacity: 0;
}

.animate-up {
  transform: translateY(15px);
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.42, 0, 0.002, 1), transform 0.65s cubic-bezier(0.42, 0, 0.002, 1);
  will-change: transform;
}
@media (max-width: 1023px) {
  .animate-up {
    transform: translate(0);
    opacity: 1;
  }
}

.animate-down {
  transform: translateY(-100%);
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.42, 0, 0.002, 1), transform 0.65s cubic-bezier(0.42, 0, 0.002, 1);
  will-change: transform;
}
@media (max-width: 1023px) {
  .animate-down {
    transform: translate(0);
    opacity: 1;
  }
}

.animate-right {
  transform: translatex(-100%);
  opacity: 0;
  transition: opacity 0.5s cubic-bezier(0.42, 0, 0.002, 1), transform 0.65s cubic-bezier(0.42, 0, 0.002, 1);
}

