body {
  background-color: #FFFFFF;
}

main {
  position: relative;
  overflow: hidden;
  z-index: 10;
}

.hide-scroll {
  overflow: hidden;
  height: 100vh;
}

.font-normal {
  font-weight: 400;
}

.font-medium {
  font-weight: 500;
}

.font-semibold {
  font-weight: 600;
}

.font-bold {
  font-weight: 700;
}

.font-extrabold {
  font-weight: 800;
}

.text-primary {
  color: #2185B2;
}

.text-secondary {
  color: #BC5B43;
}

.text-neutral-500 {
  color: #6D6E71;
}

.bg-primary {
  background-color: #2185B2;
}

.bg-secondary {
  background-color: #BC5B43;
}

.bg-orange {
  background-color: #F68621;
}

.bg-black {
  background-color: #2D2B40;
}

.reveal-image {
  position: relative;
  --scaleX: 1;
  --scaleFull: 1.2;
  overflow: hidden;
}
.reveal-image:before {
  background-color: #FFFFFF;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  content: "";
  transform: scaleX(var(--scaleX));
  transform-origin: left;
  transition: 0.5s all ease-out;
  z-index: 10;
}
.reveal-image img {
  transition: 0.5s all ease-out;
  transform: scale(var(--scaleFull));
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.text-underline {
  position: relative;
  display: inline-flex;
}
.text-underline svg {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -10px;
}
@media (max-width: 1023px) {
  .text-underline svg {
    width: 114px;
    height: 12px;
  }
}

.moving-item {
  will-change: transform;
}